@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Open Sans",sans-serif}h1{margin-bottom:1rem}.container{background-color:#f7f6f4;height:100vh;margin:0 auto;max-width:800px}select{border:0px;background-color:#fff;outline:0px;width:200px}button{background-color:white;border:1px solid black;cursor:pointer;display:inline-block;font-size:0.8rem;height:2rem;margin:1rem 0.5rem 1rem 0.5rem;width:6rem}.row{display:flex;flex-flow:wrap}.col-1{width:8.33333%}.col-2{width:16.66667%}.col-3{width:25%}.col-4{width:33.33333%}.col-5{width:41.66667%}.col-6{width:50%}.col-7{width:58.33333%}.col-8{width:66.66667%}.col-9{width:75%}.col-10{width:83.33333%}.col-11{width:91.66667%}.col-12{width:100%}.formStepOne{flex-direction:column;align-items:center}.formStepOne .overprint{margin-bottom:1rem}.formStepOne .overprint .overprint__photo{background-image:url("https://picsum.photos/200/200");height:200px;margin:1rem 0;width:200px}.formStepTwo{flex-direction:column;align-items:center}.formStepTwo .photosToChoose{color:white}.formStepTwo .photosToChoose .overprint__photo{background-image:url("https://picsum.photos/200/200");display:inline-block;height:200px;margin:1rem 0.5rem;width:200px}.formStepTwo .photosToChoose .overprint__photo2{background-image:url("https://picsum.photos/300/300");display:inline-block;height:200px;margin:1rem 0.5rem;width:200px}.formStepTwo .photosToChoose .overprint__photo3{background-image:url("https://picsum.photos/400/400");display:inline-block;height:200px;margin:1rem 0.5rem;width:200px}.chosenImage{height:200px;margin:1rem 0.5rem;width:200px}.toDataEntry{display:block;margin-top:1rem}input{display:block;margin:0.5rem 0.5rem}label{margin:0 0.5rem}.nextButton{display:block;margin:0.5rem 0}.toThankYou{display:block;margin-top:1rem}

